.intro {
  width: 100%;
  height: 100%;
  min-height: 600px;

  &__inner {
    position: relative;
  }

  &__photo {
    position: relative;
    width: 50%;
  }

  &__img {
    position: absolute;
    bottom: 0;
    left: 100px;
    z-index: -2;
    height: 100%;

  }

  &__descr {
    padding-right: 100px;
    display: flex;
    flex-direction: column;
    align-items: end;
    width: 100%;
  }

  &__hello {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 500;
    line-height: 27px;
    letter-spacing: 0.04em;

    color: var(--light-color);
  }

  &__name {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 800;
    font-size: 100px;
    line-height: 122px;
    letter-spacing: 0.06em;

    color: var(--blue-color);
  }

  &__text {
    margin: 0;
    width: 550px;

    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    letter-spacing: 0.04em;
  }
}