.swiper {

  &__intro {
    position: absolute;
    right: 0;
    bottom: -40%;
    z-index: 1;

    &-wrapper {
      max-width: 700px;
    }

    &-slide {
      margin: 0;
      max-width: 250px;
      height: 150px;
      overflow: hidden;
    }

    &-img {
      width: 250px;
      height: 150px;
  
      border-radius: 5px;
    }
  }

  &__portfolio {

    &-wrapper {
      width: 100%;
    }

    &-img {
      width: 600px;
      height: 400px;
    }
  }

  &__testimonials {
    text-align: center;
    margin: 50px;

    &-img {
      width: 100px;
      height: 100px;

      border-radius: 100px;
    }

    &-text {
      padding: 0 70px;
    }

    &-arrow {
      transform: translateY(100%);
    }
  }
  

  

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;

    color: var(--light-color);
  }

  &__view {
    margin: 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.04em;

    color: var(--blue-color);
  }

  
}


.photos {
  display: flex;
  justify-content: end;
  // width: 50%;

  &__title {
    margin: 0;
    width: 40%;
    margin-bottom: 25px;
  }

  &__link {
    margin: 0;
    padding-top: 20px;
    color: var(--blue-color);
  }
}