.graph-modal {
  background: rgba(#1f2022, .9);

  &__close {
    width: 30px;
    height: 30px;
    top: -32px;
    right: -32px;
    z-index: 1;
    position: absolute;

    &-white {
      transition: transform .1s linear;

      &:hover {
        transform: rotate(90deg);
      }
    }

    @include mobile {
      top: 20px;
      right: 20px;
    }
  }

  &__container {
    padding: 30px;
    width: 500px;
    box-shadow: var(--shadow);
    border-radius: 5px;

    @include mobile {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 100;
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    margin-bottom: 3px;
    font-size: 23px;
    color: var(--pink-color);
    font-weight: 500;
  }

  &__text {
    margin-bottom: 30px;
    font-size: 15px;
    line-height: 1.2;
    color: var(--dark-color);
    font-weight: 500;
  }

  &__input {
    margin-bottom: 15px;
    padding: 15px 18px;
    width: 100%;
    height: 45px;

    border: 1px solid hsla(0, 0%, 7%, .2);
    border-radius: 5px;

    &:focus {
      outline: 0;
      border-color: var(--pink-color);
    }
  }
}

.form {
  position: relative;

  
}